import jquery = require("jquery");
import {AButton} from "./abstract/a-button";
import {EBasicColorStatus} from "../component/enum/e-basic-color-status";

/**
 * @classdesc Renderiza um botão simples.
 * 
 * <img src="../../doc_assets/buttom_exemple.png" />
 * 
 * @example
 * 
 * import {Button} from "mvcomponents/button";
 * import {EBasicColorStatus} from "mvcomponents/component";
 * 
 * let button:Button = new Button("Label")
 *     .setIcon("mv-basico-adicionar")
 *     .setColor(EBasicColorStatus.DANGER)
 *     .setLabel("Modificar Label")
 *     .setTooltip("Tooltip do Button");
 *
 * this.append(button);
 * 
 * @export
 * @extends {AButton}
 * @param {string} [label=null] Rótulo do Button.
 * @memberof module:mvcomponents/button
 */
export class Button extends AButton {
	constructor(label: string = "") {
		super("button");
		this.setLabel(label);
		jquery(this.element)
			.addClass("btn btn-default btn-sm Button ButtonComponent");
	}
	/**
	 * Altera o icone que será exibido pelo Button.
	 * 
	 * @example
	 * 
	 * let button1:Button = new Button("com mv-icons").setIcon("mv-hosp-pertences");
	 * let button2:Button = new Button("").setIcon("mv-basico-adicionar");
	 * 
	 * @param {string} psrc Icone que será exibido.
	 * @return {Button}
	 */
	public setIcon(psrc: string): this {
		if (jquery(this.element).find("span.iconeinput").length === 0) {
			jquery(this.element)
				.prepend(`<span class="iconeinput ${psrc}"></span>`);
		} else {
			var classes = jquery(this.element)
				.find("span.iconeinput")
				.attr("class");
			jquery(this.element)
				.find("span.iconeinput")
				.removeClass()
				.addClass(`iconeinput ${psrc}`);
		}
		return this;
	}
	/**
	 * Informa a cor do botão.
	 * 
	 * Obs.: Utiliza o EBasicColorStatus do grupo 'mvcomponents/component' para definir a cor do Button.
	 * 
	 * @example
	 * 
	 * let button1:Button = new Button("DANGER").setColor(EBasicColorStatus.DANGER);
	 * let button2:Button = new Button("SUCCESS").setColor(EBasicColorStatus.SUCCESS);
	 * let button3:Button = new Button("INFO").setColor(EBasicColorStatus.INFO);
	 * let button4:Button = new Button("PRIMARY").setColor(EBasicColorStatus.PRIMARY);
	 * let button5:Button = new Button("WARNING").setColor(EBasicColorStatus.WARNING);
	 * 
	 * @param  {EBasicColorStatus} color EBasicColorStatus do grupo 'mvcomponents/component' para definir a cor do Button
	 * @return {Button}
	 */
	public setColor(color: EBasicColorStatus): this {       
		jquery(this.element)
			.removeClass(
				this.getColors()
			)
			.addClass(`btn-${EBasicColorStatus[color].toLowerCase()}`);
		return this;
	}
	/**
	 * Atribui ou substitui o rótulo do Button
	 * 
	 * @example
	 * 
	 * let button:Button = new Button("Label Original");
	 * button.setLabel("Label Modificado");
	 * 
	 * @param  {string} label Texto que deve substituir a label do Button.
	 * @return {Button}
	 */
	public setLabel(label: string): this {
		let element = jquery(this.element);
		if (!label) {
			this
				.addStyleName("no-label")
				.removeStyleName("has-label");
			element.find("span.btnLabel").remove();
		} else {
			if (element.find("span.btnLabel").length === 0) {
				element
					.append(`<span class="btnLabel cposi_1 cposis"></span>`);
			}
			element.find("span.btnLabel").text(label);
			this
				.addStyleName("has-label")
				.removeStyleName("no-label");
		}
		return this;
	}
	/**
	 * Atribui ou substitui a classe de estilo do rótulo do Button
	 * 
	 * @example
	 * 
	 * let button:Button = new Button("Label Original");
	 * button.setLabelClass("hidden-xs");
	 * 
	 * @param  {string} classe de estilo que deve substituir a classe de estilo do label do Button.
	 * @return {Button}
	 */
	public setLabelStyleName(labelClass: string): this {
		let element = jquery(this.element);
		element.find("span.btnLabel:first")
			.removeClass()
			.addClass(`btnLabel ${labelClass}`);
		return this;
	}
	/**
	 * Atribui mais classes de estilo do rótulo do Button
	 * 
	 * @example
	 * 
	 * let button:Button = new Button("Label Original");
	 * button.addLabelClass("hidden-xs");
	 * 
	 * @param  {string} classe de estilo que deve ser adicionada ao label do Button.
	 * @return {Button}
	 */
	public addLabelStyleName(labelClass: string): this {
		let element = jquery(this.element);
		element.find("span.btnLabel:first")
			.addClass(labelClass);
		return this;
	}        
	/**
	 * Atribui ou substitui o Tooltip do Button
	 * 
	 * @example
	 * 
	 * let button:Button = new Button("tooltip");
	 * button.setTooltip('Tooltip do button');
	 * 
	 * @param {string} tooltip Texto que deve ser exibido no Tooltip.
	 * @return {Button}
	 */
	public setTooltip(tooltip: string): this {
		if ( tooltip ) {
			jquery(this.element).attr("title", tooltip);
		} else {
			jquery(this.element).removeAttr("title");
		}
		return this;
	}
}
